.radio-input {
  display: grid;

  grid-template-columns: 30px 1fr;

  @media (min-width: $vp-768) {
    grid-template-columns: 40px 1fr;
  }
}

.radio-input__input {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  margin: 0;

  border: 1px solid $color-very-light-grey;
  border-radius: 50%;
  cursor: pointer;

  appearance: none;

  @media (min-width: $vp-768) {
    width: 40px;
    height: 40px;
  }

  &:hover {
    border-color: $color-silver;
  }

  &:disabled {
    border-color: $color-very-light-grey;
    opacity: 0.3;

    pointer-events: none;
  }

  &::after {
    content: "";

    display: none;
    width: 13px;
    height: 13px;

    background-color: $color-apple;
    border-radius: 50%;

    @media (min-width: $vp-768) {
      width: 18px;
      height: 18px;
    }
  }
}

.radio-input__input:checked::after {
  display: block;
}

.radio-input__text {
  margin-top: 6px;
  padding-left: 15px;

  line-height: 18px;
  color: $color-nero;
  text-transform: uppercase;

  cursor: pointer;

  justify-self: start;

  @media (min-width: $vp-768) {
    padding-left: 20px;

    line-height: 30px;
  }

  &--color-black {
    color: $color-black;
  }
}

.radio-input__input:disabled + .radio-input__text {
  cursor: default;
  opacity: 0.3;
}
